const gameContainerEl = document.getElementById('8');
// –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
const container = document.createElement('div');
container.className = 'input-container';
document.body.appendChild(container);

// –ó–∞–≥–æ–ª–æ–≤–æ–∫
const heading = document.createElement('h2');
heading.textContent = '–í–≤–µ–¥—ñ—Ç—å 3 —á–∏—Å–ª–∞';
heading.className = 'header-title';
container.appendChild(heading);

// –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —ñ–Ω–ø—É—Ç—ñ–≤
const inputsDiv = document.createElement('div');
inputsDiv.className = 'inputs';
container.appendChild(inputsDiv);

// –Ü–Ω–ø—É—Ç–∏
const inputs = [];
for (let i = 0; i < 3; i++) {
  const input = document.createElement('input');
  input.type = 'number';
  input.placeholder = '–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ';
  input.className = 'input-number';
  inputs.push(input);
  inputsDiv.appendChild(input);
}

// –†–µ–∑—É–ª—å—Ç–∞—Ç
const result = document.createElement('div');
result.className = 'result-text';
result.textContent = '–ù–∞–π–±—ñ–ª—å—à–µ —á–∏—Å–ª–æ, —è–∫–µ –≤–∏ –≤–≤–µ–ª–∏ - (—á–∏—Å–ª–æ)';
container.appendChild(result);

// –õ–æ–≥—ñ–∫–∞
inputs.forEach(input => {
  input.addEventListener('input', () => {
    const values = inputs.map(i => parseFloat(i.value)).filter(n => !isNaN(n));
    if (values.length > 0) {
      const max = Math.max(...values);
      result.textContent = `–ù–∞–π–±—ñ–ª—å—à–µ —á–∏—Å–ª–æ, —è–∫–µ –≤–∏ –≤–≤–µ–ª–∏ - ${max}`;
    } else {
      result.textContent = '–ù–∞–π–±—ñ–ª—å—à–µ —á–∏—Å–ª–æ, —è–∫–µ –≤–∏ –≤–≤–µ–ª–∏ - (—á–∏—Å–ª–æ)';
    }
  });
});
